<template>
  <div class="topBtn" @click="animateTop">
    <img src="@/assets/images/backTop.png" alt="" />
  </div>
</template>

<script>
import { animateTopMixin } from "@u/animateTopMixin";
export default {
  mixins: [animateTopMixin],
  data() {
    return {
      backTop: false,
    };
  },
  mounted() {
    // addEventListener()
    // 
    //   <p>  <div>  <p> ,  "click" 
    //    <p>  <div> 
    //    <div>   <p> 
    // addEventListener('',()=>{},false) false- 
    window.addEventListener("scroll", this.handleScroll, true);
    this.$once("hook:destroyed", () => {
      window.removeEventListener("scroll", this.handleScroll, true);
    });
  },
  methods: {
    handleScroll() {
      let scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      scrollTop > 30 ? (this.backTop = true) : (this.backTop = false);
    },
  },
};
</script>

<style lang="scss" scoped>
.topBtn {
  position: fixed;
  right: 3rem;
  bottom: 2rem;
  img {
    width: 3rem;
    height: 4rem;
  }
}
@media (max-width: 1024px) {
  .topBtn {
    img {
      width: 3rem;
      height: 5rem;
    }
  }
}
</style>